import React from "react";
import styled from "styled-components";
import axios from "axios";
import { Link } from "react-router-dom";

class Album extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: ""
    };
  }
  async componentDidMount() {
    const url = this.props.album.tracklist;
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const {
      data: { data }
    } = await axios.get(`${proxyurl}${url}`, {
      method: "GET",
      headers: {
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    this.setState({
      data: data
    });
  }
  render() {
    const {
      props: { album }
    } = this;
    return (
      <Contaier>
        <Poster poster={album.cover_medium}></Poster>
        {this.state.data ? (
        <Link
          to={{
            pathname: `/album/${album.title}`,
            state: {
              data: this.state.data,
              album:album,
              poster:album.cover_medium,
              title:album.title
            }
          }}
          style={{ textDecoration: "none", color: "black" }}
        >
          <Name>{album.title}</Name>
        </Link>
         ) : null}
      </Contaier>
    );
  }
}
export default Album;
const Contaier = styled.div`
  width: 100%;
  font-size: 1.1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  /* margin: 0 30px; */
  background-color: rgba(240, 240, 240, 0.5);
`;
const Name = styled.div`
  /* background-color:yellow; */
  padding: 10px;
`;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 120px;
  height: 120px;
`;
